.products_head {
    position: relative;
    padding: 2.4rem;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.16);
    height: 11rem;
}
.products_head--style-1 {
    height: auto;
    background: var(--color-white);
    padding-right: 3.2rem;
}
.products_head-content {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    gap: 1.6rem;
}
.products_head .back {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 2.4rem;
    height: 2.4rem;
    color: var(--color-grey-3);
    transition: color 0.3s ease-in-out;
}
.products_head .back:hover {
    color: var(--color-orange-100);
}
.products_head .back .icon {
    width: 1.2rem;
    height: 1.2rem;
}
.products_head .title {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    gap: 1.2rem;
    line-height: 1;
}
.products_head .title h1:not(:last-child) {
    margin-bottom: 0;
}
.products_head .title .number {
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    padding: 0.4rem 1.2rem;
    background: var(--color-orange-100);
    border-radius: 0.8rem;
    color: var(--color-white);
    font-size: 1.4rem;
    font-weight: 700;
}
.products_head-search {
    flex: 1;
}
.products_head-search input {
    background: var(--color-grey-7);
}
.products_head-search input::-webkit-input-placeholder {
    color: var(--color-grey-3);
}
.products_head-search input::-moz-placeholder {
    opacity: 1;
    color: var(--color-grey-3);
}
.products_head-search input:-moz-placeholder {
    color: var(--color-grey-3);
}
.products_head-search input:-ms-input-placeholder {
    color: var(--color-grey-3);
}
.products_head-search input.placeholder {
    color: var(--color-grey-3);
}
.products_head-search input > .icon {
    color: var(--color-grey-3);
}
.products_head-brand-types {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
.products_head-brand-types .brand-type {
    position: relative;
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-decoration: none;
    height: 3rem;
    padding: 0.8rem;
    color: var(--color-grey-3);
    font-size: 1.3rem;
    font-weight: 400;
    border: 0.1rem solid currentColor;
    transition: all 0.3s ease-in-out;
}
.products_head-brand-types .brand-type:not(:first-child) {
    border-left: none;
}
.products_head-brand-types .brand-type.active,
.products_head-brand-types .brand-type:hover {
    background: var(--color-navy);
    color: var(--color-white);
    border-color: var(--color-navy);
}
.products_head-brand-types .brand-type:first-child {
    border-radius: 0.8rem 0 0 0.8rem;
}
.products_head-brand-types .brand-type:first-child:last-child {
    border-radius: 0.8rem;
}
.products_head-brand-types .brand-type:last-child {
    border-radius: 0 0.8rem 0.8rem 0;
}
.products_head-brand-types .brand-type:last-child:first-child {
    border-radius: 0.8rem;
}
.products_active-remove-all {
    color: var(--color-orange-100);
    font-size: 1.3rem;
}
.products_active-filter {
    margin-bottom: 1.6rem;
}
.products_active-filters {
    position: relative;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
    align-items: baseline;
}
.products_active-filters .icon {
    width: 1.2rem;
    height: 1.2rem;
}
.products_active-filters button {
    border: none;
    padding: 0;
    margin: 0;
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
.products_active-filters button.products_active-remove-all {
    background: transparent;
}
.products_active-filters button.products_active-remove-all:hover {
    opacity: 0.8;
}
.products_active-filter {
    position: relative;
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    gap: 0.6rem;
    height: 3rem;
    background: var(--color-white);
    border-radius: 10rem;
    color: var(--color-navy);
    border: 0.1rem solid var(--color-navy);
    padding: 0.4rem 0.8rem;
    transition: all 0.3s ease-in-out;
}
.products_active-filter button {
    background-color: var(--color-navy);
    color: var(--color-white);
    padding: 0.3rem;
    border-radius: 100%;
    width: 1.2rem;
    height: 1.2rem;
    transition: all 0.3s ease-in-out;
}
.products_active-filter button .icon {
    width: 100%;
    height: 100%;
}
/*
    .update_products_content .products_active-filter .products_active-remove{background-color: var(--color-navy);}
    .update_products_content .products_active-filter .products_active-remove:hover{background-color: var(--color-white);}
    */
.products_active-filter:hover {
    background: var(--color-navy);
    color: var(--color-white);
}
.products_active-filter:hover button {
    background: #56ccf2;
    color: var(--color-navy);
}
.sidebar-hidden .pc {
    width: calc((100% - (3 * 1.6rem)) / 4);
}
.pc {
    position: relative;
    width: calc(100% / 4);
    min-width: 24rem;
    height: 34.3rem;
    background: var(--color-white);
    border: 0.1rem solid var(--color-grey-5);
    letter-spacing: 0.02em;
    line-height: 1.05;
    z-index: 0;
}
.pc:hover {
    z-index: 1;
}
.pc-title {
    position: relative;
    margin: 1.5rem 0;
    width: 100%;
    font-size: 1.3rem;
    line-height: 1;
    font-weight: 700;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.pc.open .pc_extra {
    opacity: 1;
    visibility: visible;
}
.pc_main {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 1.6rem;
    z-index: 0;
}
.pc_head {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    margin-bottom: 0.8rem;
}
.pc_head > :not(:last-child) {
    margin-right: 0.6rem;
}
.pc_body {
    position: relative;
}
/*.pc_body:hover .pc_links{opacity:1!important;visibility:visible!important}*/
.pc-dropship {
    position: relative;
    width: 2.4rem;
    height: 2.4rem;
    min-width: 2.4rem;
    color: var(--color-grey-4);
}
.pc-dropship--active {
    color: var(--color-green-1);
}
.pc-dropship .icon_wrap {
    width: 100%;
    height: 100%;
}
.pc-status {
    position: relative;
    flex: 1;
    display: flex;
    align-items: center;
    align-content: center;
    font-size: 0.9rem;
    font-weight: 700;
    text-transform: uppercase;
    gap: 0.2rem;
}
.pc-status .v-line {
    position: relative;
    display: inline-block;
    margin: 0 0.25rem;
    height: 1.6rem;
    width: 0.1rem;
    background: var(--color-grey-5);
}
.pc-status .icon {
    position: relative;
}
.pc-status .icon_wrap {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 2.4rem;
    height: 2.4rem;
    min-width: 2.4rem;
}
.pc-status--connected {
    color: #2f80ed;
}
.pc-status--connected .icon {
    width: 1.4rem;
    height: 1.4rem;
}
.pc-status--not-connected {
    color: #828282;
}
.pc-status--not-connected .icon {
    height: 1.4rem;
    width: 1.4rem;
}
.pc-status--pending {
    color: #828282;
}
.pc-status--pending .icon {
    height: 1.3rem;
    width: 1.3rem;
}
.pc-status--cancel {
    color: var(--color-red);
}
.pc-status--cancel .icon {
    width: 1.6rem;
    height: 1.6rem;
}
.pc-status--pfs {
    color: #2f80ed;
}
.pc-status--pfs .icon {
    width: 1.4rem;
    height: 1.4rem;
}
.pc-status--rfs {
    color: #2f80ed;
}
.pc-status--rfs .icon {
    width: 1.4rem;
    height: 1.4rem;
}
.pc-status-button {
    position: relative;
    border: none;
    height: 2.4rem;
    border-radius: 0.4rem;
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    padding: 0.4rem 0.8rem;
    color: var(--color-white);
    background: var(--color-navy);
}
.pc-status-button--connect {
    background: var(--color-navy);
}
.pc-status-button--remove {
    background: #eb5757;
}
.pc-status-button--accept {
    background: #219653;
}
.pc-status-button--close {
    background: #eb5757;
}
.pc-info {
    position: absolute;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    right: 0.6rem;
    top: 0.6rem;
    z-index: 2;
    padding: 0.1rem;
    background: var(--color-white);
    border-radius: 100%;
    border: none;
    margin: 0;
    width: 1.4rem;
    height: 1.4rem;
    color: #2f80ed;
    cursor: pointer;
}
.pc-info .icon {
    width: 100%;
    height: 100%;
}
.pc_slider {
    position: relative;
    width: 100%;
    height: 17.1rem;
    overflow: hidden;
}
.pc_slider .swiper-container {
    height: 100%;
}
.pc_slider .swiper-slide {
    height: 100%;
    width: 100%;
}
.pc_slider .swiper-slide .image {
    position: relative;
    width: 100%;
    height: 100%;
}
.pc_slider .swiper-slide .image picture {
    width: 100%;
    height: 100%;
}
.pc_slider .swiper-slide .image picture img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    border: 1px solid #f7f7f7;
    overflow: hidden;
    border-radius: 4px;
}
.pc_var {
    position: relative;
    display: flex;
    font-size: 1.1rem;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.12);
}
.pc_var:not(:last-child) {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.12);
    margin-bottom: 1.2rem;
}
.pc_var > * {
    padding: 0.8rem;
}
.pc_var > :first-child {
    padding-left: 0;
}
.pc_var-title {
    flex: 1;
    border-right: 0.1rem solid var(--color-grey-5);
}
.pc_var-numbs {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    padding-right: 0;
    gap: 0.6rem;
}
.pc_var-new {
    color: var(--color-white);
    background: var(--color-red);
    padding: 0.2rem 0.6rem;
    border-radius: 10rem;
    text-transform: uppercase;
    font-size: 0.8rem;
    font-weight: 700;
}
.pc_vals {
    position: relative;
    display: flex;
    font-size: 1.1rem;
    line-height: 1;
    margin: 0 -0.6rem;
}
.pc_vals .image {
    width: 0.6rem;
    height: 1.1rem;
    min-width: 0.6rem;
}
.pc_vals .tooltip-icon:before {
    display: none;
}
.pc_vals-title {
    display: flex;
    gap: 0.6rem;
}
.pc_vals-block {
    flex: 1;
    max-width: calc(100% / 3);
    padding: 0 0.6rem;
}
.pc_vals-block--profit .pc_vals-price {
    color: var(--color-green-1);
}
.pc_vals-price {
    position: relative;
    width: 100%;
    padding-top: 0.4rem;
    font-weight: 700;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.pc_links {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    gap: 0.8rem;
    background: rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
    opacity: 0;
    visibility: hidden;
}
.pc_links a {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    border-radius: 100%;
    width: 3.2rem;
    height: 3.2rem;
    background: var(--color-white);
    color: var(--color-grey-3);
    transition: all 0.3s ease-in-out;
    text-decoration: none;
}
.pc_links a .icon {
    width: 1.6rem;
    height: 1.6rem;
}
.pc_links a:hover {
    color: var(--color-white);
    background: var(--color-yellow);
}
.pc_extra {
    position: absolute;
    display: flex;
    flex-direction: column;
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    background: var(--color-white);
    padding: 4rem 1.6rem 1.6rem;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}
.pc_extra-close {
    position: absolute;
    width: 1.6rem;
    height: 1.6rem;
    right: 2rem;
    top: 2rem;
    cursor: pointer;
    z-index: 1;
}
.pc_extra .variants {
    position: relative;
}
.pc_extra .variants .variant {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    padding: 0.8rem;
    text-decoration: none;
    background: var(--color-grey-7);
    border-radius: 0.4rem 0.4rem 0 0;
}
.pc_extra .variants .variant:not(:last-child) {
    margin-bottom: 0.2rem;
}
.pc_extra .variants .variant_image {
    position: relative;
    width: 2.4rem;
    min-width: 2.4rem;
    border-radius: 0.4rem;
    overflow: hidden;
    margin-right: 1.6rem;
}
.pc_extra .variants .variant_text {
    flex: 1;
    font-size: 1.1rem;
}
.pc_categories {
    height: 5rem;
    margin-top: auto;
}
.pc_categories-title {
    font-size: 1rem;
    margin-bottom: 0.8rem;
}
.pc_categories-items {
    position: relative;
    height: 3rem;
    display: flex;
    border: 0.1rem solid var(--color-orange-100);
}
.pc_categories-item {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: flex-start;
    padding: 0.2rem 0.8rem;
    font-size: 1rem;
    text-decoration: none;
    color: var(--color-orange-100);
}
.pc_categories-item:last-child {
    font-weight: 700;
    color: var(--color-navy);
}
.pc_categories-item:not(:last-child):after {
    content: '';
    position: absolute;
    width: 0.8rem;
    height: 100%;
    right: -0.2rem;
    background-image: url(../images/pc_category-decor.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center right;
}
.pc_tabs {
    flex: 1;
    max-height: 20rem;
}
.pc_tabs .tabs_body {
    display: flex;
    flex-direction: column;
    max-height: calc(100% - 3rem);
    height: 100%;
}
.pc_tabs .tab {
    height: 100%;
}
.pc_tabs .tab_inner {
    height: 100%;
    overflow: auto;
}
.pc_tabs .tab_inner-text {
    height: 100%;
    overflow: auto;
    max-height: calc(100% - 5.5rem);
}
/*Card Empty*/
.product-card-empty,
.card-empty {
    position: relative;
    width: 100%;
    border-radius: 0.4rem;
    background: var(--color-white);
    padding: 2rem 1.6rem;
}
.product-card-empty_head,
.card-empty_head {
    position: relative;
    padding-bottom: 2rem;
    border-bottom: 0.1rem solid rgba(0, 0, 0, 0.16);
    text-transform: uppercase;
    font-size: 1.2rem;
}
.product-card-empty_body,
.card-empty_body {
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-top: 1.6rem;
    font-size: 1.6rem;
}
.product-card-empty_body p,
.card-empty_body p {
    margin-bottom: 1.6rem;
}
.product-card-empty_body a,
.card-empty_body a {
    color: var(--color-orange-100);
    text-decoration: none;
    margin-bottom: 3.2rem;
}
.product-card-empty_body .image,
.card-empty_body .image {
    width: 15.6rem;
    margin: 0 auto;
}
.table tbody td:last-child .card-empty {
    color: var(--color-black);
}
.table tbody .card-empty .image {
    width: auto;
    height: auto;
}
/*Card Empty end */
.product {
    padding: 2.4rem 3.2rem 2.4rem 2.4rem;
}
.product_breadcrumbs {
    display: none;
}
.product_head {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    gap: 1.6rem;
    margin-bottom: 2.4rem;
}
.product_head .back {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 2.4rem;
    height: 2.4rem;
    color: var(--color-grey-3);
    transition: color 0.3s ease-in-out;
}
.product_head .back:hover {
    color: var(--color-orange-100);
}
.product_head .back .icon {
    width: 1.2rem;
    height: 1.2rem;
}
.product_head .title {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    gap: 1.6rem;
}
.product_head .title h1:not(:last-child) {
    margin-bottom: 0;
}
.product_head .view-list {
    margin-left: auto;
}
.product-new {
    position: relative;
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    color: var(--color-white);
    background: var(--color-red);
    padding: 0.4rem 1.3rem;
    border-radius: 10rem;
    text-transform: uppercase;
    font-size: 1rem;
    font-weight: 700;
    height: 2.4rem;
}
.product_main {
    position: relative;
    display: flex;
    flex-wrap: wrap;
}
.product_info {
    flex: 1;
    padding-left: 7.2rem;
    letter-spacing: 0.04em;
}
.product_info > :not(:last-child) {
    margin-bottom: 2.4rem;
}
.product_info strong {
    font-weight: 700;
}
.product_info-buttons {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
}
.product_info-buttons .button {
    width: auto;
    height: 4.8rem;
    flex: 1;
    letter-spacing: 0.03em;
}
.product_info-buttons .button.large {
    letter-spacing: 0.04em;
}
.product_info-buttons .button:not(:last-child) {
    font-size: 1.8rem;
}
.product_info-buttons .button:last-child {
    flex: none;
    width: 100%;
    height: 3rem;
}
.product_info h2 {
    letter-spacing: 0.025em;
}
.product_info .for {
    position: relative;
}
.product_info .for .line:not(:last-child) {
    margin-bottom: 0;
}
.product_vars {
    padding-top: 6.4rem;
    position: relative;
    width: 100%;
    margin-bottom: 6.4rem;
}
.product_shipping {
    position: relative;
    width: 100%;
}
.product_extra {
    --gap: 1.2rem;
    letter-spacing: 0.03em;
    width: 100%;
}
.product_extra:not(:last-child) {
    margin-bottom: 2.4rem;
}
.product_extra--style-1 .product_extra-body {
    background: var(--color-white);
}
.product_extra h4 {
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: flex-start;
    gap: 0.6rem;
}
.product_extra h4:not(:last-child),
.product_extra p:not(:last-child) {
    margin-bottom: 1.5rem;
}
.product_extra strong {
    font-weight: 700;
}
.product_extra-head {
    position: relative;
    margin-bottom: 0.8rem;
    letter-spacing: 0.04em;
}
.product_extra-body {
    position: relative;
    padding: 2.4rem;
    border-radius: 0.8rem;
    background: var(--color-orange-tint-2);
    color: var(--color-grey-2);
}
.product_extra-body .icon {
    width: 1.7rem;
    height: 2rem;
}
.product_about {
    --gap: 0.4rem;
    width: 100%;
}
.product_about-content {
    background: var(--color-orange-tint-2);
    border-radius: 0.4rem;
    padding: 2.4rem 1.6rem;
    min-height: 100%;
}
.product_about-image {
    margin-bottom: 1.6rem;
}
.product_about-image .image {
    position: relative;
    width: 8rem;
    height: 8rem;
    overflow: hidden;
    border-radius: 100%;
}
.product_about-title {
    margin-bottom: 1.6rem;
    color: var(--color-orange-100);
}
.product_about-items:not(:last-child) {
    margin-bottom: 4rem;
}
.product_about-item:not(:last-child) {
    margin-bottom: 2.4rem;
}
.product_about-item-title {
    margin-bottom: 0.8rem;
    font-weight: 700;
}
.product_about-item-wrap {
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
}
.product_about-item-wrap a {
    height: 2.4rem;
    border-radius: 10rem;
    background: var(--color-orange-100);
