    opacity: 0;
    cursor: pointer;
}
.header_menu-wrap input[type='checkbox']:checked ~ .hamburger-lines .line1,
.filters .tab_wrap input[type='checkbox']:checked ~ .hamburger-lines .line1 {
    transform: rotate(45deg);
}
