    max-width: 100%;
    height: 100%;
    position: relative;
    background: #fbf5f0;
}
.ob-head {
    position: relative;
    padding: 2.4rem;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.16);
    height: 10rem;
    background: var(--color-white);
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.ob-head h1 {
    margin: 0;
    padding: 0;
    color: #283b49;
    letter-spacing: 0.2px;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
}
.ob-body .form-wrapper {
    margin: 0 auto;
    text-align: center;
    padding-top: 32px;
    min-width: 576px;
    max-width: 576px;
    width: 100%;
}
.ob-body .form-area {
    border-radius: 8px;
    padding: 24px 24px 32px;
    background: #fff;
}
.ob-body .form-input .form-control {
    background: #fffcf9;
    border: 1px solid #f3e0d2;
    border-radius: 4px;
    height: 4rem;
    padding: 1rem 1.2rem;
    display: block;
    width: 100%;
    margin-top: 0.6rem;
    margin-bottom: 1.6rem;
}
.ob-body .form-password {
    position: relative;
}
.ob-body .form-password .form-control {
    background: #fbf5f0;
    border: 1px solid #fbf5f0;
    padding-right: 45%;
}
.ob-body .category-form-input .form-input {
    width: 100%;
    text-align: left;
}
.ob-body .category-form-input {
    display: flex;
    justify-content: space-between;
    gap: 1.8rem;
    align-items: baseline;
    margin-bottom: 1rem;
}
.ob-body svg.icon-eye {
    position: absolute;
    right: 13px;
    width: 2.5rem;
    opacity: 0.64;
    height: 2rem;
    top: 17px;
}
.ob-body svg.icon-eye:hover {
    opacity: 0.5;
}
.pwd-indicator {
    position: absolute;
    right: 50px;
    top: 16px;
    color: #354253;
    letter-spacing: 0.2px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
}
.ob-body .check-box {
    text-align: left;
    color: #354253;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
}
.ob-body .check-box a {
    text-decoration: none;
    color: #bd6f34;
}
.check-box .checkbox-text:before {
    content: '';
    position: absolute;
    width: 1.8rem;
    height: 1.8rem;
    left: 0;
    top: 0.1rem;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    transition: all 0.15s linear;
    background-color: currentColor;
    -webkit-mask-image: url(../images/icons/icon-check--false.svg);
    mask-image: url(../images/icons/icon-check--false.svg);
}
.check-box .checkbox {
    width: 100%;
    height: 100%;
    align-items: center;
    color: #bd6f34;
    cursor: pointer;
    margin: 12px 0;
    display: block;
}
.checkbox-text {
    padding-left: 3rem;
}
.checkbox input {
    color: #7194b5;
}
.ob-body .checkbox span {
    color: #354253;
    line-height: 20px;
}
.check-box input:checked ~ .checkbox-text:before {
    -webkit-mask-image: url(../images/icons/icon-check--true.svg);
    mask-image: url(../images/icons/icon-check--true.svg);
}
.ob-body .button.large {
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    height: 48px;
}
.verify_email p {
    color: #512f16;
    letter-spacing: 0.3px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
}
.verify_email .button.large {
    font-size: 16px;
    height: 40px;
}
.verify-title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
    width: 100%;
    position: relative;
    margin-bottom: 1.6rem;
    gap: 1rem;
}
.verify-title .icon-mail {
    width: 2.5rem;
    height: 2rem;
}
.verify-title h2 {
    margin: 0;
    padding: 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 32px;
    color: #000000;
    letter-spacing: 0.4px;
}
.ob-body .form-input .is-invalid {
    border-color: var(--color-red);
    margin-bottom: 0;
}
.ob-body .invalid-feedback {
    color: var(--color-red);
    display: block;
    margin: 0.4rem 0 1rem;
}
.personalize .p-text {
    color: #354253;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}
.personalize_selection {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 1.6rem;
}
.personalize_selection .pse_item {
    background: #fbf5f0;
    border-radius: 4px;
    min-height: 182px;
    padding: 0 12px;
    text-align: center;
    width: 100%;
    margin-top: 2.4rem;
    border: 2px solid #fbf5f0;
}
.personalize_selection .pse_item:hover {
    opacity: 0.8;
    cursor: pointer;
}
.personalize_selection .pse_item.active {
    background: #ffffff;
    border: 2px solid #d18d58;
}
.personalize_selection .pse_item.active h3 {
    color: #354253;
}
.personalize_selection .pse_item svg {
    width: 5.6rem;
    height: 5.6rem;
    stroke: none;
    margin-top: 2.4rem;
}
.personalize_selection .pse_item h3 {
    letter-spacing: 0.2px;
    text-transform: uppercase;
    color: #808084;
    font-weight: 700;
    margin: 0;
    padding: 0;
    margin-top: 1rem;
    font-size: 14px;
    line-height: 14px;
}
.personalize_selection .pse_item p {
    color: #808084;
    letter-spacing: 0.2px;
    font-weight: 400;
    font-size: 12px;
    margin-bottom: 1.6rem;
    margin-top: 1rem;
    line-height: 14px;
}
.info-integrate {
    margin-top: 4px;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #808084;
    display: block;
    width: 100%;
}
.text-navy {
    color: var(--color-navy);
}
.radiobox-group .radiobox-text:before {
    content: '';
    position: absolute;
    color: #bd6f34;
    width: 2rem;
    height: 2rem;
    left: 0;
    top: 0;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    transition: all 0.15s linear;
    background-color: currentColor;
    -webkit-mask-image: url(../images/icons/icon-radio-false.svg);
    mask-image: url(../images/icons/icon-radio-false.svg);
}
.radiobox-group .radiobox input:checked ~ .radiobox-text:before {
    -webkit-mask-image: url(../images/icons/icon-radio-true.svg);
    mask-image: url(../images/icons/icon-radio-true.svg);
}
.radiobox-group .radiobox {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 50%;
    height: 2.5rem;
    cursor: pointer;
    line-height: 17px;
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: flex-start;
    width: 50%;
    height: 2.5rem;
    cursor: pointer;
    line-height: 17px;
}
.radiobox-group .radiobox input:checked ~ .radiobox-text {
    color: #354253;
}
.select_info-integrate {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
