.header .dropdown_body {
    position: absolute;
    top: calc(100% + 0.8rem);
    left: 0;
    min-width: var(--d-body-width);
    border-radius: 0.4rem;
    background: var(--color-white);
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.16);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}
.header .dropdown_body:before {
    content: '';
    position: absolute;
    left: 0;
    top: -0.8rem;
    height: 0.8rem;
    width: 100%;
}
